import React from "react";
import { Row, } from "reactstrap";
import { connect } from 'react-redux';
import moment from 'moment'

const Negotiations = (props) => {

    const ss = props.state;
    const off = props.offer;
    const time = props.timestr
    const date = props.date
    const type = props.type
    console.log(off)    
    const doubleclick = ()=>{
        console.log('hsgdgd nma')
    }
    return (
        <>
            {(type==='message'?
                <Row style={{ width: '100%', backgroundColor: '', display: 'flex', justifyContent: ss === 'left' ? 'flex-start' : 'flex-end', paddingLeft: '0px', alignItems: '', margin: '0px' }}>
                    <div style={{ display: ss === 'left' ? 'flex' : 'none', backgroundColor: 'transparent', width: '9px', flexDirection: 'column' }}>
                        <div style={{ width: '100%', height: '15px', backgroundColor: 'transparent' }}></div>
                        <div style={{ width: '100%',flex: 1, backgroundColor: 'transparent' }}></div>
                        <div style={{ width: '100%', height: '9px', backgroundColor: 'orange', background: 'linear-gradient(to bottom right, transparent 0%, transparent 50%, #007eec 50%, #007eec 100%)' }}></div>
                    </div>
                    <div className='chat' title={moment(date).format('Do MMM YY')} id={off} onDoubleClick={doubleclick} style={{ borderBottomLeftRadius: ss === 'left' ? '0px' : '20px', borderBottomRightRadius: ss === 'left' ? '20px' : '0px', backgroundColor: ss === 'left' ? '#007eec' : '#e0e0e0', color: ss === 'left' ? '#fff' : '' }} >
                        <div>
                            <span>{off}</span>
                        </div>
                        <div style={{display: 'flex',justifyContent: 'flex-end',backgroundColor: ''}}><p className='mb- mt-' style={{ fontSize: '12px',marginBottom: '0px' }}>{time}</p></div>
                    </div>
                    <div style={{ display: ss === 'left' ? 'none' : 'flex', backgroundColor: 'transparent', width: '9px', flexDirection: 'column' }}>
                        <div style={{ width: '100%', height: '15px', backgroundColor: 'transparent' }}></div>
                        <div style={{ width: '100%', flex: 1, backgroundColor: 'transparent' }}></div>
                        <div style={{ width: '100%', height: '9px', backgroundColor: 'orange', background: 'linear-gradient(to bottom left, transparent 0%, transparent 50%, #e0e0e0 50%, #e0e0e0 100%)' }}></div>
                    </div>
                </Row>
            :
                (type === 'image' ?
                    <Row style={{ width: '100%', backgroundColor: '', display: 'flex', justifyContent: ss === 'left' ? 'flex-start' : 'flex-end', paddingLeft: '0px', alignItems: '', margin: '0px' }}>
                        <div style={{ display: ss === 'left' ? 'flex' : 'none', backgroundColor: 'transparent', width: '9px', flexDirection: 'column' }}>
                            <div style={{ width: '100%', height: '15px', backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', flex: 1, backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', height: '9px', backgroundColor: 'orange', background: 'linear-gradient(to bottom right, transparent 0%, transparent 50%, #007eec 50%, #007eec 100%)' }}></div>
                        </div>
                        <div className='chat' title={moment(date).format('Do MMM YY')} onDoubleClick={doubleclick} style={{ borderBottomLeftRadius: ss === 'left' ? '0px' : '20px', borderBottomRightRadius: ss === 'left' ? '20px' : '0px', backgroundColor: ss === 'left' ? '#007eec' : '#e0e0e0', color: ss === 'left' ? '#fff' : '' }} >
                            <div>
                                <a href={props.image} target="_blank" rel="noreferrer" ><img alt='Message_Image' src={props.image} style={{maxHeight: '300px',maxWidth: '300px',borderRadius: '20px'}} onError={(e) => { e.target.src = 'https://i.ibb.co/drffP5L/place.png' }} /></a>
                                <p>Image</p>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'flex-end', backgroundColor: '' }}><p className='mb- mt-' style={{ fontSize: '12px', marginBottom: '0px' }}>{time}</p></div>
                        </div>
                        <div style={{ display: ss === 'left' ? 'none' : 'flex', backgroundColor: 'transparent', width: '9px', flexDirection: 'column' }}>
                            <div style={{ width: '100%', height: '15px', backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', flex: 1, backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', height: '9px', backgroundColor: 'orange', background: 'linear-gradient(to bottom left, transparent 0%, transparent 50%, #e0e0e0 50%, #e0e0e0 100%)' }}></div>
                        </div>
                    </Row>
                    :
                    <Row style={{ width: '100%', backgroundColor: '', display: 'flex', justifyContent: ss === 'left' ? 'flex-start' : 'flex-end', paddingLeft: '0px', alignItems: '', margin: '0px' }}>
                        <div style={{ display: ss === 'left' ? 'flex' : 'none', backgroundColor: 'transparent', width: '9px', flexDirection: 'column' }}>
                            <div style={{ width: '100%', height: '15px', backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', flex: 1, backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', height: '9px', backgroundColor: 'orange', background: 'linear-gradient(to bottom right, transparent 0%, transparent 50%, #007eec 50%, #007eec 100%)' }}></div>
                        </div>
                        <div className='chat' title={moment(date).format('Do MMM YY')} onDoubleClick={doubleclick} style={{ borderBottomLeftRadius: ss === 'left' ? '0px' : '20px', borderBottomRightRadius: ss === 'left' ? '20px' : '0px', backgroundColor: ss === 'left' ? '#007eec' : '#e0e0e0', color: ss === 'left' ? '#fff' : '' }} >
                            <div style={{backgroundColor: 'white',padding: '10px',display: 'flex',color: 'black'}}>
                                <a target="_blank" rel="noreferrer" href={props.file} ><i className='ni ni-folder-17 mt-1 mr-2'></i><span>{off}</span></a>
                                <a href={props.file} download ><span style={{fontSize: '20px'}} ><i className='ni ni-cloud-download-95 mt-1 ml-2'></i></span></a>
                            </div>
                            <div style={{ display: 'flex', justifyContent: 'flex-end', backgroundColor: '' }}><p className='mb- mt-' style={{ fontSize: '12px', marginBottom: '0px' }}>{time}</p></div>
                        </div>
                        <div style={{ display: ss === 'left' ? 'none' : 'flex', backgroundColor: 'transparent', width: '9px', flexDirection: 'column' }}>
                            <div style={{ width: '100%', height: '15px', backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', flex: 1, backgroundColor: 'transparent' }}></div>
                            <div style={{ width: '100%', height: '9px', backgroundColor: 'orange', background: 'linear-gradient(to bottom left, transparent 0%, transparent 50%, #e0e0e0 50%, #e0e0e0 100%)' }}></div>
                        </div>
                    </Row>
                )
            )}
        </>
    );
};

export const mapStateToProps = state => {
    // console.log("this is the state", state);
    return {
        // adminValue: state.switchRoutes.admin,
        // userValue: state.switchRoutes.user,
        // fullname: state.tempUser,
        username: state.userName,
        token: state.token,
    }
}

export default connect(mapStateToProps)(Negotiations);
